nav {
  margin: 20px 0px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  color: white;
}

nav .icon {
  height: 100%;
  width: auto;
}

nav .Links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

nav .Socials {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

nav .Socials button {
  padding: auto;
  color: white;
}

nav a {
  color: white;
  text-decoration: none;
  margin: 0px 15px;
}

nav .Link-Container.active {
  border-bottom: 3px solid white;
}
